main {
    & > section {
        position: relative;

        div.container {
            width: 100%;
            height: 100%;
            @include width();
        }
    }
    .social-bar {
        height: 10vh;
        display: flex;
        width: 100%;
        align-items: center;
        font-size: 2em;
        justify-content: space-between;
        pointer-events: auto;
        z-index: 2;
        margin: $small-padding 0 0;
        a {
            color: hsl(210, 100%, 80%);
        }
        @include mq(small) {
            width: $width; max-width: $max-width;
            margin:auto;
            padding: 0;
        }
        @include mq(medium) {
            position: absolute;
            flex-direction: column;
            right: 0;
            bottom: $padding;
            align-items: start;
            height: 5em;
            z-index: 3;
            width: initial;
            height: 100vh;
            justify-content: flex-end;
            align-items: center;
            

            & > a {
                color: hsl(210, 100%, 25%);
                transition: color 0.3s cubic-bezier(0.165, 0.84, 0.44, 1);
                &+a {
                    margin-top: .5em;
                }

                &:hover {
                    color:hsl(210, 100%, 60%);
                }
            }
        }
        @include mq(xl) {
            right: calc(((100% - #{$max-width}) / 2));
        }
    }

    
}

#index {
    #frontpage {
        overflow: hidden;
        padding-top: 100px;
        position: relative;

        background: hsl(210, 100%, 8%);
        @include mq(large) {
            background: none;
        }

        .container {
            padding-top: 0;
            display: grid;
            grid-gap: 1em;
            @include mq(xl) {
                @include mqh(medium) {
                    grid-gap: $padding;
                }
            }
            
            position: relative;
            @include width();

            @include mq(small) {
                grid-template-columns: repeat(auto-fit, minmax(400px, 1fr));
            }

            & > .main-info {
                padding-top: $padding;
                padding-right: 1em;
                padding-bottom: 4rem;
            }
            & > .tertiary-info {
                @include mq(small) {
                    padding-bottom: $padding;
                }
                @include mq(large) {
                    width: calc(100% - #{$padding});
                }
                
                & > div {
                    padding: 1rem 0;
                    
                    margin-top: 1em;
                    border-top-right-radius: $border-radius;
                    border-bottom-right-radius: $border-radius;
                    @include mq(large) {
                        background-color: hsl(331, 100%, 96%);
                        padding: $padding;
                        height: calc(100% - 4rem);
                        width: calc(100% - 2rem);
                    }
                    @include mq(xl) {
                        @include mqh(medium) {
                            margin-top: $padding;
                        }
                    }

                    h1 {
                        color: hsl(331, 50%, 60%);
                        font-size: 2em;
                    }
                    p {
                        color: hsl(331, 50%, 47%);
                        font-size: 1em;
                    }
                }
            }
            h1 {
                font-size: 2em;
                @include mq(small) {
                    font-size: 2.8em;
                }

                color: hsl(210, 100%, 90%);
            }
            
            p {
                font-size: 1.05em;
                margin-top: 1.4em;
                @include mq(small) {
                    font-size: 1.25em;
                    margin-top: 1.5em;
                }
                @include mqh(medium) {
                    margin-top: 2em;
                }
                
                color: hsl(210, 100%, 90%);

                &+p {
                    margin-top: 1em;
                }
            }

            button {
                margin-top: 2em;
                @include mqh(medium) {
                    margin-top: 2.5em;
                }
            }
        }

        .movement-arrow {
            
            width: 4em;
            display: none;
            align-items: center;
            justify-content: center;

            position: absolute;
            bottom: 0;
            @include mq(small) {
                display: flex;
            }
            @include mq(xl) {
                left: calc(50% + 0.5em);
                @include mqh(medium) {
                    left: calc(50% + 2em);
                }
            }
            
            right: initial;
            color: hsl(210, 100%, 25%);

            &:hover {
                color:hsl(210, 100%, 60%);
            }
            i {
                transition: color 0.3s cubic-bezier(0.165, 0.84, 0.44, 1);
                font-size: 1.5em;
                animation: sdb04 2s infinite;

                @keyframes sdb04 {
                    0% {
                        transform: translate(0, 0);
                      }
                    20% {
                      transform: translate(0, .25em);
                    }
                    40% {
                        transform: translate(0, 0);
                      }
                  }
            }
        }
    }

    #gallery {
        position: relative;
        background-color: hsl(210, 100%, 20%);
        & > h1 {
            color: hsl(210, 100%, 90%);
        }
    }
    .grid {
        z-index: 2;
        @include width();
        @include mq(medium) {
            padding: $padding 0;
        }
    }
}